@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;1,300&display=swap');

*{
    margin: 0%;
    padding: 0%;
    font-family: "Roboto",sans-serif;

}

a{
    color: white;
    text-decoration: none;

}


$bg:rgb(16,16,16);

.Header  {
    height : 100px;
    background-color: $bg;
    display: flex;
    align-items: center;
    padding: 3rem;
    box-sizing: border-box;
    justify-content: space-between;

    >img {
        height: 1000%;
        
    }
    >div{
        
        width: 100%;
        margin-left: 3vmax;

        >a{
            margin:1.5vmax;
            font-size: 1.3rem;
            font-weight: 300;
            font-family:Georgia, 'Times New Roman', Times, serif;
            transition: transform 0.2s;

            &:hover{
                color: gray;
                
            }
        }
    }
    >svg{
        color:white;
        font-size: 2rem;
        cursor: pointer;
        margin-left: 15px;
        transition: transform 0.2s;

        &:hover{
            color: red;
          
        }
    }
}






 